.sale {
  position: relative;

  margin: 0 auto;
  padding: 0;
  // padding-top: 48px;
  padding-bottom: 48px;

  background-image: url(../images/main-sale/bg-image/bg-mob@1x.png);

  @include retina {
    background-image: url(../images/main-sale/bg-image/bg-mob@2x.png);
  }

  @include tablet {
    // padding: 120px 0;
    padding-bottom: 120px;

    background-image: url(../images/main-sale/bg-image/bg-tab@1x.png);

    @include retina {
      background-image: url(../images/main-sale/bg-image/bg-tab@2x.png);
    }
  }

  @include desktop {
    background-image: url(../images/main-sale/bg-image/bg-desk@1x.png);

    @include retina {
      background-image: url(../images/main-sale/bg-image/bg-desk@2x.png);
    }
  }
}

.sale__container {
  margin: 0 auto;
  padding-left: 17px;
  padding-right: 18px;
  width: 320px;

  @include tablet {
    width: 768px;
    padding-left: 80px;
    padding-right: 80px;
  }

  @include desktop {
    width: 1280px;
    padding-left: 40px;
    padding-right: 40px;
  }
}

.sale__list {
  padding: 0;
  gap: 24px;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 24px;
  @include tablet {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: center;
    gap: 38px;
    margin-bottom: 28px;
  }
  @include desktop {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
    margin-bottom: 24px;
  }
}

.sale__item {
  @media screen and (max-width: 767px) {
    &:nth-child(even) {
      display: none;
    }
    &:nth-child(1) {
      order: 1;
    }
    &:nth-child(5) {
      order: 2;
    }
    &:nth-child(3) {
      order: 3;
    }
    &:nth-child(7) {
      order: 4;
    }
  }
  @include tablet {
    &:nth-child(1) {
      order: 1;
    }
    &:nth-child(2) {
      order: 2;
    }
    &:nth-child(3) {
      order: 5;
    }
    &:nth-child(4) {
      order: 8;
    }
    &:nth-child(5) {
      order: 3;
    }
    &:nth-child(6) {
      order: 4;
    }
    &:nth-child(7) {
      order: 7;
    }
    &:nth-child(8) {
      order: 6;
    }
  }
  @include desktop {
    &:nth-child(1) {
      order: 1;
    }
    &:nth-child(2) {
      order: 2;
    }
    &-:nth-child(3) {
      order: 3;
    }
    &:nth-child(4) {
      order: 4;
    }
    &:nth-child(5) {
      order: 5;
    }
    &:nth-child(6) {
      order: 6;
    }
    &:nth-child(7) {
      order: 7;
    }
    &:nth-child(8) {
      order: 8;
    }
  }
}
