.item-2,
.item-4,
.item-6,
.item-8 {
  @media screen and (max-width: 767px) {
    display: none;
  }
}

.sale__item {
  width: 100%;

  @include tablet {
    width: calc((100% - 38px) / 2);
  }

  @include desktop {
    width: calc((100% - 60px) / 4);
  }
}

.sale__title {
  position: relative;
  font-family: 'Roboto';
  font-size: 24px;
  line-height: calc(28 / 24);
  font-weight: 600;
  color: $white-color;
  // margin-bottom: 35px;
  padding-bottom: 31px;
  margin-left: 0px;

  @include tablet {
    font-size: 56px;
    line-height: calc(66 / 56);
    padding-bottom: 72px;

    // margin-bottom: 51px;
    // margin-left: 142px;
  }

  @include desktop {
    // margin-bottom: 16px;
    padding-bottom: 59px;
    // margin-left: 142px;
  }
}
.sale__title::after {
  height: 31px;
  bottom: 0;
  position: absolute;
  content: '';
  // @media screen and (max-width: 532px) {
  //   max-width: 100vw;
  // }
  @media screen and (max-width: 767px) {
    width: 100%;
    left: 0;
    background-image: url('../images/blog-article/top-line.svg'),
      url('../images/blog-article/bottom-line.svg');
  }
  // @media screen and (min-width: 533px) {
  //   left: -5.5%;
  // }
  @media screen and (min-width: 768px) {
    height: 72px;
    width: 111%;
    left: -5.5%;
    background-image: url('../images/blog-article/top-line-tablet.svg'),
      url('../images/blog-article/bottom-line-tablet.svg');
    background-position-y: -150%, -100%;
  }
  @media screen and (min-width: 1280px) {
    height: 59px;
    width: 107%;
    left: -3.5%;
    background-image: url('../images/blog-article/top-line-dt.svg'),
      url('../images/blog-article/bottom-line-dt.svg');
    background-position-y: 150%, 100%;
  }
  background-repeat: no-repeat, no-repeat;
  background-position-x: left;
  background-position-y: 80%, 65%;
  background-size: 100%;
}

.sale__link {
  display: block;
}

.sale__thumb {
  position: relative;
  content: '';
}

.sale__img {
  display: block;
  max-width: 100%;
  height: auto;
  border-radius: 12px;
  margin-bottom: 8px;
}

.sale__description {
  padding: 0 16px 20px 8px;
}

.sale__text {
  font-family: 'Roboto';
  font-size: 22px;
  line-height: calc(26 / 22);
  font-weight: 600;
  color: $text-accent-color;
  margin-bottom: 8px;
}

.sale__price {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.sale__oldprice {
  font-family: 'Roboto';
  font-size: 14px;
  line-height: calc(14 / 14);
  font-weight: 600;
  color: $old-price-text-color;
  text-decoration: line-through;
}

.sale__newprice {
  font-family: 'Roboto';
  font-size: 20px;
  line-height: calc(20 / 20);
  font-weight: 600;
  color: $text-accent-color;
}

.sale__btn {
  display: block;
  margin: auto;
  cursor: pointer;
  border: none;
  padding: 16px 48px;
  border-radius: 16px;
  background-color: $btn-view-all-color;
  font-family: 'Roboto';
  font-weight: 700;
  color: $btn-text-color;
  font-size: 16px;
  line-height: calc(19 / 16);
  box-shadow: none;

  @include tablet {
    font-size: 28px;
    line-height: 1.17;
    min-width: 195px;
    height: 65px;
    // margin-bottom: 120px;
  }
  transition: box-shadow 250ms cubic-bezier(0.4, 0, 0.2, 1);
  &:hover,
  &:focus {
    box-shadow: $btn-view-all-hover-shadow;
  }
}

