.accessories {
  padding-top: 48px+104px;
  padding-bottom: 48px;
  background-color: $page-bgn-color;
  background-image: url(../images/take-order-img/bgi1x.jpg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  width: 100%;
  @media screen and (min-device-pixel-ratio: 2),
    screen and (min-resolution: 192dpi),
    screen and (min-resolution: 2dppx) {
    background-image: url(../images/take-order-img/bgimob1x.jpg);
  }

  @media screen and (min-width: 768px) {
    background-image: url(../images/take-order-img/bgitab1x.jpg);

    @media screen and (min-device-pixel-ratio: 2),
      screen and (min-resolution: 192dpi),
      screen and (min-resolution: 2dppx) {
      background-image: url(../images/take-order-img/bgitab2x.jpg);
    }
  }

  @media screen and (min-width: 1280px) {
    background-image: url(../images/take-order-img/bgi1x.jpg);

    @media screen and (min-device-pixel-ratio: 2),
      screen and (min-resolution: 192dpi),
      screen and (min-resolution: 2dppx) {
      background-image: url(../images/take-order-img/bgi2x.jpg);
    }
  }
  @include tablet {
    padding-top: 120px+136px;
    padding-bottom: 120px;
  }
  
}

.container-accessories {
  max-width: 480px;
  margin-left: auto;
  margin-right: auto;
  @media screen and (min-width: 768px) {
    max-width: 768px;
    padding-left: 40px;
    padding-right: 40px;
    padding-bottom: 104px;
  }
  @media screen and (min-width: 1280px) {
    max-width: 1280px;
  }
}

.accessories__btn-list {
  width: 100%;
  width: 272px;
  margin-left: auto;
  margin-right: auto;
  //height: 94px;
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 32px;
  justify-content: center;
  align-items: center;
  gap: 24px;
  @include mobile {
    //display: flex;
    //flex-wrap: wrap;
    gap: 24px;
    //width: calc((100% - 72px) / 2);
    flex-direction: row;
    flex-basis: calc((100% - 118px) / 2);
  }
  @include tablet {
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 72px;
    width: 687px;
    flex-wrap: nowrap;
    justify-content: space-between;
    gap: 53px;
  }
  @include desktop {
    width: 801px;
    margin-bottom: 72px;
    gap: 43px;
  }
}
