.accessories__btn {
  font-weight: 700;
  font-size: 16px;
  line-height: 1.17;
  //letter-spacing: 0.06em;
  vertical-align: center;
  color: $text-accent-color;
  background-color: $page-bgn-color;
  cursor: pointer;
  width: 124px;
  height: 51px;
  border-color: $btn-border-color;
  text-align: center;
  align-items: center;
  margin-right: auto;
  margin-left: auto;
  border-radius: 16px;
  border: 1px solid rgba(134, 229, 67, 1);
  //padding: 16px 74px;
  //text-decoration: none;
  gap: 10px;
  transition: box-shadow 250ms cubic-bezier(0.4, 0, 0.2, 1),
  background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);

  @include tablet {
    width: 132px;
  }
  @include desktop {
    width: 168px;
  }
}

.accessories__btn:hover,
.accessories__btn:focus {
  color: $btn-text-color;
  background-color: $btn-color;
  border: 4px;
  box-shadow: 0px 8px 20px rgba(134, 229, 67, 0.5),
    0px 4px 10px rgba(255, 255, 255, 0.1);
}

.accessories__btn:not(:last-child) {
  margin-right: 20px;
}

.accessories__title {
  position: relative;
  font-weight: 500;
  //margin-top: 0;
  padding-left: 16px;
  //margin-bottom: 0;
  font-size: 24px;
  padding-bottom: 27px;
  line-height: 1.17;
  //margin-left: auto;
  //margin-right: auto;
  color: $nav-menu-text-color;
  //text-align: left;
  //padding-top: 0;
  @include tablet {
    font-size: 56px;
    padding-left: 142px;
    padding-bottom: 51px;
  }
  @include desktop {
    padding-bottom: 51px;
    padding-left: 142px;
    font-size: 56px;
  }
}
.accessories__title::after {
  width: 100vw;
  height: 31px;
  bottom: 0;
  position: absolute;
  content: '';
  @media screen and (max-width: 767px) {
    // width: 107%;
    left: -1px;
    background-image: url('../images/contacts/top_line_mob.svg'),
      url('../images/contacts/bottom_line_mob.svg');
  }
  @include tablet {
    height: 72px;
    width: 111%;
    left: -5.5%;
    background-image: url('../images/contacts/top_line_tablet.svg'),
      url('../images/contacts/bottom_line_tablet.svg');
    background-position-y: 50%, 200%;
  }
  @include desktop {
    height: 59px;
    width: 107%;
    left: -3.5%;
    background-image: url('../images/contacts/top_line_desk.svg'),
      url('../images/contacts/bottom_line_desk.svg');
    background-position-y: 45%, -30%;
  }
  background-repeat: no-repeat, no-repeat;
  background-position-x: left;
  background-position-y: 50%, 40%;
  background-size: 100%;
}

