.accessories__list {
  width: 100%;
  //margin: o auto;
  justify-content: center;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  //flex-direction: column;
  gap: 20px;
  @include tablet {
    //width: 100%;
    //flex-wrap: wrap;
    flex-direction: row;
    flex-basis: calc((100% - 118px) / 2);
  }
  @include desktop {
    margin-left: auto;
    margin-right: auto;
    //width: 100%;
    flex-direction: row;
    flex-wrap: wrap;
    flex-basis: calc((100% - 60px) / 4);
  }
}

.accessories__list {
