      order: 5;
    }
    .accessories__picture:nth-child(10) {
      order: 6;
    }
    .accessories__picture:nth-child(13) {
      order: 7;
    }
    .accessories__picture:nth-child(14) {
      order: 8;
    }
    .accessories__picture:nth-child(3) {
      order: 9;
    }
    .accessories__picture:nth-child(4) {
      order: 10;
    }
    .accessories__picture:nth-child(7) {
      order: 11;
    }
    .accessories__picture:nth-child(8) {
