.accessories__pic {
  border-radius: 12px;
  //box-shadow: #86E543;
  //width: 100%;
  //overflow: hidden;
}

.accessories__pic:hover {
  box-sizing: 4px;
  border-color: $btn-border-color;
}

.accessories__link {
  display: flex;
  flex-direction: column;
  @include mobile {
    width: 100%;
    margin-left: auto;
    margin-right: auto;
  }
  @include tablet {
    height: 100%;
  }
  @include desktop {
    height: 100%;
  }
}

.accessories__link:hover,
.accessories__link:focus {
  display: block;
}
.accessories__overlay {
  position: relative;
  overflow: hidden;
  border-radius: 12px;
  width: 100%;
  height: auto;
  display: block;
  //text-align: left;
  @include desktop {
    height: auto;
  }
}

.accessories__overlay-text {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #01383a;
  transform: translateY(101%);
  transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
  font-weight: 400;
  font-size: 14px;
  line-height: 1.17;
  white-space: pre-wrap;
  margin: 0;
  color: $white-color;
  padding: 16px 16px 52px 16px;

  border: 1px solid rgba(134, 229, 67, 1);
  text-align: left;
  vertical-align: top;
}

.accessories__picture:hover .accessories__overlay-text {
  transform: translateY(0);
}

