.accessories__block {
  padding-top: 8px;
  width: 285px;
  max-height: 88px;
  margin-bottom: 36px;
  @include tablet {
    padding-top: 8px;
    margin-bottom: 44px;
  }
  @include desktop {
    margin-bottom: 44px;
  }
}

.accessories__name {
  font-weight: 500;
  font-size: 22px;
  line-height: 1, 17;
  text-align: left;
  color: $text-accent-color;
  margin-bottom: 11px;
  //margin-left: 8px;
  max-width: 261px;
}

.accessories__prices {
  display: flex;
  align-items: center;
  //gap: 80px;
  width: 285px;
}

.accessories__old-price {
  font-weight: 500;
  font-size: 14px;
  width: 100%;
  text-decoration: line-through;
  line-height: 1;
  text-align: left;
  color: $old-price-text-color;
}

.accessories__price {
  font-weight: 500;
  width: 100%;
  font-size: 20px;
  line-height: 1;
  text-align: right;
  color: $text-accent-color;
}

.pagination {
  display: flex;
  gap: 16px;
  justify-content: center;
}

.bullet {
  width: 16px;
  height: 16px;
  background: $white-color;
  //   opacity: 1;
  display: block;
  border-radius: 30px;
  cursor: pointer;
}

.bullet1 {
  width: 40px;
}

.pagination:hover > .bullet1 {
  animation-name: bullet1;
  animation-duration: 2000ms;
  animation-timing-function: linear;
  padding-bottom: 10px;
  animation-iteration-count: infinite;
}

@keyframes bullet1 {
  0% {
    width: 40px;
  }

  50% {
    width: 16px;
  }

  100% {
    width: 16px;
  }
}

.pagination:hover > .bullet2 {
  animation-name: bullet2;
  animation-duration: 2000ms;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
}

@keyframes bullet2 {
  0% {
    width: 16px;
  }

  50% {
    width: 40px;
  }

  100% {
    width: 16px;
  }
}

.pagination:hover > .bullet3 {
  animation-name: bullet3;
  animation-duration: 2000ms;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
}

@keyframes bullet3 {
  0% {
    width: 16px;
  }

  50% {
    width: 16px;
  }

  100% {
    width: 40px;
  }
}
